import 'normalize.css'
import 'animate.css'
import 'fullpage.js/dist/fullpage.min.css'
import './css/start.css'
import './css/main.css'
import fullpage from 'fullpage.js/dist/fullpage.min.js'
import $ from 'jquery'

$(function(){
    // var sUserAgent = navigator.userAgent.toLowerCase();
    // var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
    // var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
    // var bIsMidp = sUserAgent.match(/midp/i) == "midp";
    // var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
    // var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
    // var bIsAndroid = sUserAgent.match(/android/i) == "android";
    // var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
    // var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
    // if (bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM) {
    //      window.location.href = 'phone.html';
    // } else {
    //     //  window.location = 'PC设备地址';
    // }

    var banner = '';
    var myFullpage = new fullpage('#fullpage', {
        anchors: ['page1', 'page2', 'page3', 'page4', 'page5', 'page6'],
        menu: '#menu',
        css3: true,
        scrollingSpeed: 1000,
        // dragAndMove: true,
        controlArrows: false,
        sectionsColor: ['#C63D0F', '#1BBC9B', '#7E8F7C'],
        navigation: true,
        resize: true, //字体大小是否随窗口缩放而缩放 默认值为false
        // continuousHorizontal: true,
        navigationTooltips: ['首页', '关于我', '技术栈',  '工作经历', '项目经历', '联系我'],
        afterLoad:function(anchorLink, index){
            //滚动到某一屏后的回调函数  
            var id = index.item.id;
            
            if(id == 'section1'){
                $('#section1 .wrap>p').removeClass('hide')
                $('#section1 .wrap>p:first-child').addClass('animated fadeInRight')
                $('#section1 .wrap>p:last-child').addClass('animated fadeInLeftBig')
            }  else {
                $('#section1 .wrap>p').addClass('hide')
                $('#section1 .wrap>p').removeClass('animated fadeInRight')
                $('#section1 .wrap>p:last-child').removeClass('animated fadeInLeftBig')
            }

            if(id == 'section3'){
                $('#section3 .first-page>h1').removeClass('hide')
                $('#section3 .first-page>p').removeClass('hide')
                $('#section3 .first-page>h1').addClass('animated fadeInDown')
                $('#section3 .first-page>p').addClass('animated fadeInDown')
            }  else {
                $('#section3 .first-page>h1').addClass('hide')
                $('#section3 .first-page>p').addClass('hide')
                $('#section3 .first-page>h1').removeClass('animated fadeInDown')
                $('#section3 .first-page>p').removeClass('animated fadeInDown')
            }

            if(id == 'section4'){
                $('#section4 .box1 h2').removeClass('hide')
                $('#section4 .box1 h2').addClass('animated fadeInDown')
                $('#section4 .box1 p').removeClass('hide')
                $('#section4 .box1 p').addClass('animated fadeInUp') 
                $('#section4 .box2 img').removeClass('hide')
                $('#section4 .box2 img').addClass('animated fadeInRight')
            }  else {
                $('#section4 .box1 h2').addClass('hide')
                $('#section4 .box1 h2').removeClass('animated fadeInDown')
                $('#section4 .box1 p').addClass('hide')
                $('#section4 .box1 p').removeClass('animated fadeInUp')
                $('#section4 .box2 img').addClass('hide')
                $('#section4 .box2 img').removeClass('animated fadeInRight')
            }

            if(id == 'section5'){
                $('#section5 .wrap>p').removeClass('hide')
                $('#section5 .wrap>p').addClass('animated lightSpeedIn')
                $('#section5 .img-box').removeClass('hide')
                $('#section5 .img-box').addClass('animated fadeInDown')
            }  else {
                $('#section5 .wrap>p').addClass('hide')
                $('#section5 .wrap>p').removeClass('animated lightSpeedIn')
                $('#section5 .img-box').addClass('hide')
                $('#section5 .img-box').removeClass('animated fadeInDown')
            }

            if(id == 'section6'){
                $('#section6 .platform').removeClass('hide')
                $('#section6 .platform').addClass('animated fadeInDownBig')
                $('#section6 .form-fill').removeClass('hide')
                $('#section6 .form-fill').addClass('animated fadeInLeftBig')
                $('#section6 textarea').removeClass('hide')
                $('#section6 textarea').addClass('animated fadeInRight')
                $('#section6 .sub-btn').removeClass('hide')
                $('#section6 .sub-btn').addClass('animated fadeInUp')
            }  else {
                $('#section6 .platform').addClass('hide')
                $('#section6 .platform').removeClass('animated fadeInDownBig')
                $('#section6 .form-fill').addClass('hide')
                $('#section6 .form-fill').removeClass('animated fadeInLeftBig')
                $('#section6 textarea').addClass('hide')
                $('#section6 textarea').removeClass('animated fadeInRight')
                $('#section6 .sub-btn').addClass('hide')
                $('#section6 .sub-btn').removeClass('animated fadeInUp')
            }
        } ,
        showActiveTooltip: true,
        slidesNavigation: true, //是否显示左右轮播的小圆点
        navigationPosition: 'right'
    });

    //首屏轮播初始化自动轮播时间
    banner = setInterval(function(){ 
        fullpage_api.moveSlideRight(); 
    }, 5000);

    //鼠标悬停在第一屏的左右按钮上和轮播内容上时，停止轮播
    $('#section0 .container').on('mouseenter', function() {
        clearInterval(banner)
    })
    //鼠标离开第一屏的左右按钮上或轮播内容上时时，开始自动轮播
    $('#section0 .container').on('mouseleave', function() {
        banner = setInterval(function(){ 
            fullpage_api.moveSlideRight(); 
        }, 5000); 
    })

    $('#section0 .slide-pre').on('click', function() {
        fullpage_api.moveSlideLeft();
    })

        $('#section0 .slide-next').on('click', function() {
        fullpage_api.moveSlideRight();
    })

    $(".fixtel").on("mouseenter",function(){
        $(".fixtel div").removeClass('hide')
    })

    $(".fixtel").on("mouseleave",function(){
        $(".fixtel div").addClass('hide')
    })

    $(".fixweixin").on("mouseenter",function(){
        $(".fixweixin div").removeClass('hide')
    })

    $(".fixweixin").on("mouseleave",function(){
        $(".fixweixin div").addClass('hide')
    })
    
})